import React from "react";
import photo from "../images/ClarkPhoto.jpg"; // How get image to load

const About = () => (
  <div>
    <h1>About Page</h1>
    <img src={photo} alt="headshot" class="img-thumbnail"></img>
    <p>My name is Brittany Clark and I live in Raleigh, NC.</p>
  </div>
);

export default About;
